.wallet-box {
    display: flex;
    flex-direction: row;
    align-items: center;
    
    border-radius: 5px;
    overflow: hidden;
    padding: 0px 10px;
}

.wallet-connect-btn {
    border: 2px solid #000;
    padding: 3px;
    color: #000;
    display: inline-block;
    width: 100px;
    height: 35px;
    background-color: #fff;
    font-size: 15px;
    cursor: pointer;
    border-radius: 3px;
}

.wallet-account {
    width: 110px;
    overflow: hidden;
    color: #999;
    margin-right: 5px;
}