.collection-meta {
    margin-bottom: 80px;
}

.title {
    font-size: 45px;
}

.description {
    color: #777;
}

.items-filter-box {
    width: 330px;
    height: 100vh;
    overflow-y: scroll;
    flex-shrink: 0;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    /* background-color: rgb(251, 253, 255); */
}

.items-box {
    border-top: 1px solid #eee;
    display: flex;
    flex-direction: row;
    align-items: flex-start;
}

.items-list {
    padding-top: 40px;
}

.items-count  {
    color: #777;
    text-align:center;
    margin: 15px;
}